<script>

export default {
  name: 'MainPage',
  props: {
    user: String
  },
  data() {
    return {
      countDownDate: new Date("Oct 1, 2022").getTime(),
      days: '00',
      hours: '00',
      minutes: '00',
      seconds: '00',
      open: false,
      msg: '',
      colors: '#' + (Math.random() * 0xFFFFFF << 0).toString(16),
      chat: [
        { nama: "Anton", waktu: "30/09/2022 12:00", ket: "Hadir", pesan: "Selamattt yaa jfklajskdlfj askdjf klasdjfkladsj flkasjdfklasjdfk asdklfj askldjfkladsj fkladsj flkdasjfkljda sklfjadsklflkadsjflkadsj flkadsjf klasdjd flk;adsj fkljads klfj daskf ja" },
        { nama: "Anton", waktu: "30/09/2022 12:00", ket: "Hadir", pesan: "Selamattt yaa" },
        { nama: "Anton", waktu: "30/09/2022 12:00", ket: "Hadir", pesan: "Selamattt yaa" },
        { nama: "Anton", waktu: "30/09/2022 12:00", ket: "Hadir", pesan: "Selamattt yaa" },
        { nama: "Anton", waktu: "30/09/2022 12:00", ket: "Hadir", pesan: "Selamattt yaa" },
        { nama: "Anton", waktu: "30/09/2022 12:00", ket: "Hadir", pesan: "Selamattt yaa" },
      ]
    }
  },
  mounted() {
    this.countDownTimer();
    this.generatorColor();
  },
  created() {
    this.countDownTimer();
  },
  watch() {
    this.countDownTimer();
  },
  methods: {
    instagramAction(data) {
      window.location = data;
    },
    copyData(data, msg) {
      navigator.clipboard.writeText(data);
      this.open = true
      this.msg = msg;
    },
    countDownTimer() {
      window.setInterval(() => {
        var now = new Date().getTime();
        var distance = this.countDownDate - now;
        var doubleDigitParse = function (digit) {
          if (digit < 10) {
            return '0' + digit;
          } else {
            return digit;
          }
        }
        this.days = Math.floor(distance / (1000 * 60 * 60 * 24));
        this.hours = doubleDigitParse(Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)));
        this.minutes = doubleDigitParse(Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)));
        this.seconds = doubleDigitParse(Math.floor((distance % (1000 * 60)) / 1000));

        if (distance < 0) {
          this.days = '00';
          this.hours = '00';
          this.minutes = '00';
          this.seconds = '00';
        }
      }, 1000)

    },

    closeModal() {
      this.open = false
    },
    generatorColor() {
      this.colors = '#' + (Math.random() * 0xFFFFFF << 0).toString(16);
    },
    

  }
}
</script>

